<template>
  <b-list-group-item style='border: none'>
    <h6 class='mb-1'>
      {{data.publicationTitle}}
    </h6>
    <p>
      Editorial: {{data.editorial}}
    </p>
    <p>Published on: {{data.publicationDate}}</p>
    <p>Publication URL: {{data.publicationURL}}</p>

    <div class='d-flex w-100, justify-content-between'>
      <small class='text-muted'>
        <b-link @click="revealEditModal(data.publicationID)">Edit</b-link>
      </small>
      <small class='text-muted'>
        <b-link @click="revealRemoveModal(data.publicationID)">Remove</b-link>
      </small>
      <b-modal :id="'modal-publication-edit-' + data.publicationID" hide-footer>
        <b-form>
          <PublicationForm :data="data"></PublicationForm>
        </b-form>
      </b-modal>
      <b-modal :id="'modal-publication-remove-' + data.publicationID" hide-footer>
        <b-form @submit="$emit('delete-row')">
          <b-button type="submit" variant="danger">Are you sure?</b-button>
        </b-form>
      </b-modal>
    </div>
  </b-list-group-item>
</template>

<script>
/* eslint-disable */

import Vue from 'vue';

import { FormPlugin, ModalPlugin, ListGroupPlugin, ButtonPlugin, LinkPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

const PublicationForm = () => import('../form/PublicationForm');

Vue.use(ListGroupPlugin);
Vue.use(FormPlugin);
Vue.use(ModalPlugin);
Vue.use(ButtonPlugin);
Vue.use(LinkPlugin);

export default {
  name: 'Publication',
  props: [
    'data',
  ],
  components: {
    PublicationForm
  },
  methods:{
    async addAuthor(evt) {
        evt.preventDefault();

        // we need to pass the id here
        // const response = await axios.post("https://reqres.in/api/account/add/publication/author", data);
    },
    async revealEditModal(_id){
      this.$bvModal.show('modal-publication-edit-' + _id);
    },
    async revealRemoveModal(_id){
      this.$bvModal.show('modal-publication-remove-' + _id);
    }
  }
}
</script>



